body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.confirmed {
  cursor: pointer;
  background-color: transparent;
}

.confirmed:hover {
  background-color: rgb(165, 98, 108);
}

.active {
  cursor: pointer;
  background-color: transparent;
}

.active:hover {
  background-color: rgb(84, 117, 155);
}
.recovered {
  cursor: pointer;
  background-color: transparent;
}

.recovered:hover {
  background-color: rgb(75, 131, 92);
}

.death {
  cursor: pointer;
  background-color: transparent;
}

.death:hover {
  background-color: rgb(133, 124, 125);
}

h1 {
  font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic,
    "AppleGothic", sans-serif;
  font-size: 3em !important;
  padding: 80px 50px;
  text-align: center;
  text-transform: uppercase;
  text-rendering: optimizeLegibility;
}
h1.elegantshadow {
  color: #131313;
  background-color: #e7e5e4;
  letter-spacing: 0.15em;
  text-shadow: 1px -1px 0 #767676, -1px 2px 1px #737272, -2px 4px 1px #767474,
    -3px 6px 1px #7b7a7a, -4px 8px 1px #7f7d7d, -5px 10px 1px #868585,
    -6px 12px 1px #8b8a89, -7px 14px 1px #949392, -8px 16px 1px #999897,
    -9px 18px 1px #a3a1a1, -10px 20px 1px #a8a6a6, -11px 22px 1px #b2b1b0,
    -12px 24px 1px #b7b6b5, -13px 26px 1px #c1bfbf, -14px 28px 1px #c6c4c4,
    -15px 30px 1px #cfcdcd, -16px 32px 1px #d4d2d1, -17px 34px 1px #dbdad9,
    -18px 36px 1px #dfdddc, -19px 38px 1px #e4e3e2;
}

.retro {
  color: #2c2c2c;
  background-color: #d5d5d5;
  letter-spacing: 0.05em;
  text-shadow: 4px 4px 0px #d5d5d5, 7px 7px 0px rgba(0, 0, 0, 0.2);
}
